                    snapType="none"
<template>
    <div class="block">
        <TitleWithButtons
            v-if="props.title"
            :title="props.title"
            :items="props.buttons"
        />

        <template v-if="props.type === 'list'">
            <div class="items-list">
                <ul class="items" v-if="data.items.length">
                    <li v-for="(item, id) in data.items"
                        :class="{ active: props.id === item.code }"
                        @click="onClick(item)"
                    >
                        <ReleasePreview
                            :row="true"
                            :item="item"
                        />
                    </li>
                </ul>

                <Button :loading="data.loading"
                    class="tertiary size-l"
                    :name="t(`blocks.releases.load`)"
                    v-if="data.loading || (data.items.length < data.total)"
                    @click="getItems"
                />

                <EmptyLabel
                    v-if="!data.items.length && !data.loading"
                    class="empty"
                    :title="props.empty?.title || t('common.empty.title')"
                    :description="props.empty?.description || t('common.empty.description')"
                />
            </div>
        </template>

        <template v-if="props.type === 'row'">
            <div class="slider-box">
                <Slider
                    v-if="data.items.length && !data.loading"
                    :colPreloadCount="props.take"
                    :items="data.items"
                    :itemsInCol="1"
                    itemWidth="146px"
                    itemHeight="208px"
                    colAlign="start"
                    colGap="8px"
                    rowGap="8px"
                    snapType="none"
                    ref="sliderRef"
                >
                    <template #default="{ item }">
                        <div class="slide" @click="onClick(item)">
                            <ReleasePreview
                                :row="false"
                                :playable="false"
                                :item="item"
                            />
                        </div>
                    </template>
                </Slider>

                <Slider
                    v-if="data.loading"
                    :colPreloadCount="props.take"
                    :items="Array.from(Array(props.take).keys())"
                    :itemsInCol="1"
                    itemWidth="146px"
                    itemHeight="208px"
                    colAlign="start"
                    colGap="8px"
                    rowGap="8px"
                    snapType="none"
                    ref="sliderRef"
                >
                    <template #default="{ item }">
                        <div class="slide" @click="onClick(item)">
                            <ReleasePreview
                                :row="false"
                                :playable="false"
                                :skeleton="true"
                            />
                        </div>
                    </template>
                </Slider>

                <EmptyLabel
                    v-if="!data.items.length && !data.loading"
                    class="empty"
                    :title="props.empty?.title || t('common.empty.title')"
                    :description="props.empty?.description || t('common.empty.description')"
                />
            </div>
        </template>
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive, computed } from 'vue';
    import api from 'üîó/api';
    import TitleWithButtons from '@/components/ui/TitleWithButtons.vue';
    import ReleasePreview from 'üîó/components/ui/ReleasePreview.vue';
    import Button from 'üîó/components/ui/Button.vue';
    import Slider from 'üîó/components/ui/Slider.vue';
    import EmptyLabel from 'üîó/components/ui/EmptyLabel.vue';
    import stores from '@/stores';

    const emit = defineEmits(['change']);

    const nav = stores.nav();


    const { t } = useI18n();

    const props = defineProps({
        empty: { type: Object, default: () => ({}) },
        title: { type: [String, null], default: null },
        params: { type: Object, default: () => ({}) },
        sort: { type: [Object, null], default: () => ({
            code: 'title',
            direction: 'asc'
        }) },
        take: { type: Number, default: 30 },
        handler: { type: [String, null], default: () => null },
        type: { type: String, default: 'list' },
        source: { type: Object, default: () => ({ store: 'releases', fnName: 'list' }) },
        buttons: { type: Array, default: () => ([]) }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        total: 0
    });


    const getItems = async () => {
        clearTimeout(data.setItemsTimeout);
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        let params = {
            ...(props.params || {}),
            skip: data.skip,
            take: props.take
        };

        if (props.sort) {
            params.sort = props.sort.code;
            params.sort_dir = props.sort.direction;
        }

        const payload = await api[props.source.store]()[props.source.fnName](params) || [];

        data.setItemsTimeout = setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...(payload.items || []));
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const onClick = (item) => {
        emit('change', item);

        if (props.handler === 'album' && item?.id) {
            nav.open('album', { id: item?.id });
        }
    }

    defineExpose({
        reload
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include default-items-list;

    .items-list {
        .items {
            padding: 16px 0;
            gap: 8px;
        }
    }

    .slider-box {
        width: 100%;
        overflow: hidden;

        .slider {
            padding: 0px 16px;
            scroll-padding-left: 16px;
            scroll-padding-right: 16px;
        }

        .loader {
            // min-height: 208px;
            margin: auto;
        }
    }

    .block {
        padding: 0 0 var(--block-gap);
        display: flex;
        flex-direction: column;
        gap: 32px;
        width: 100%;
        justify-content: start;


        .title {
            padding: 0 16px;
        }
    }
</style>