<template>
    <ItemDetail :previewHeight="420" v-if="data.ready">
        <template v-slot:background>
            <Img
                v-if="data.item?.profileImage"
                :preview="data.item?.profileImage?.crop"
                :original="data.item?.profileImage?.original"
                :alt="data.item.name"
                :skeleton="true"
            />
        </template>
        <template v-slot:preview>
            <div class="name"
                v-if="data.item?.name"
                @click="openArtist"
            >{{ data.item.name }}</div>

            <div class="buttons">
                <Button :name="t('player.shuffle')" class="tertiary active size-m" icon="random"/>
                <Button :name="t('player.play')" class="size-m" icon="play"/>
            </div>
        </template>
        <template v-slot:detail>
            <Tracks
                v-if="data.item?.id"
                type="row"
                :take="12"
                :params="{
                    contributors: data.item.id
                }"
                :sort="{
                    code: 'ratingTotal',
                    direction: 'desc'
                }"
                :buttons="[{ name: 'blocks.tracks.all', page: 'artist-tracks', params: { code: data.item.code } }]"
                title="blocks.tracks.popular-tracks"
            />
            

            <Releases
                v-if="data.item?.id"
                type="row"
                :take="8"
                :params="{
                    contributors: data.item.id
                }"
                :sort="{
                    code: 'releaseDate',
                    direction: 'desc'
                }"
                :buttons="[{ name: 'blocks.releases.all', page: 'artist-releases', params: { code: data.item.code } }]"
                title="blocks.releases.latest-releases"
            />


            <!-- <EmptyLabel
                class="empty"
                :title="t(`pages.dictionary.empty.title`)"
                :description="t(`pages.dictionary.empty.description`)"
            /> -->
        </template>
    </ItemDetail>

    <Ready page="artist" :init="init" />
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive } from 'vue';
    import api from 'üîó/api';
    import stores from '@/stores';
    import Ready from 'üîó/components/ui/Ready.vue';
    import Img from 'üîó/components/ui/Img.vue';
    import Button from 'üîó/components/ui/Button.vue';
    import ItemDetail from 'üîó/components/ui/ItemDetail.vue';
    import EmptyLabel from 'üîó/components/ui/EmptyLabel.vue';

    import Releases from 'üîó/components/blocks/Releases.vue';
    import Tracks from 'üîó/components/blocks/Tracks.vue';
    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();

    const locale = stores.locale();
    const tg = stores.tg();
    const modals = stores.modals();

    const props = defineProps({
        code: { type: String }
    });

    const data = reactive({
        ready: false,
        item: null
    });

    const init = async () => {
        data.item = await api.artists().get('cmhryr5hl014coi13tbvq6rxm' || props.code);
        data.ready = true;

        console.log(data.item)
    }

    const openArtist = () => {
        modals.open('artist', { data: { code: 'cmhryr5hl014coi13tbvq6rxm' || props.code } })
    }

    onMounted(async () => {
        tg.showBackButton();
    });

    onBeforeUnmount(() => {
        tg.hideBackButton();
    });
</script>


<style lang="scss" scoped>
    .name {
        font: var(--font-h4);
        color: white; //var(--white-500);
        font-weight: bold;
        text-align: center;
        -webkit-text-stroke: 1px rgb(0, 0, 0, .2);
    }

    .buttons {
        min-height: 38px;
        display: flex;
        flex-direction: row;
        gap: 8px;
        align-items: center;
        justify-content: center;

        button {
            min-width: 132px;
        }
    }

    .empty {
        margin: auto;
        min-height: 100px;
    }
</style>